import React, { Component, Fragment } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import CustomRoute from './routes/CustomRoute';
import { getTokenFromStorage } from './utils/helpers';
import { getUserFullInfo, closeModal } from './store/actions';
import { getUserData } from './store/selectors/user';
import Vendors from './pages/Vendors/Vendors';
import EditProduct from './pages/EditProduct';
import Auth from './pages/Auth/index';
import EditVendor from './pages/EditVendor';
import Menu from './pages/Menu';
import Cart from './pages/Cart';
import CheckoutStatus from './pages/CheckoutStatus';
import TopBar from './common/TopBar';
import PaperModal from './common/components/Modal';
import AddBalance from './pages/AddBalance';
import AdminPage from './pages/Admin';

import Products from './pages/Products';

import Header from './root/Header';
import Footer from './root/Footer';

const newConfig = [
  {
    path: '/auth',
    // exact: true,
    sidebar: () => null,
    main: () => <Auth />,
  },
  {
    path: '/vendors',
    secure: true,
    exact: true,
    top: () => null,
    main: () => <Vendors />,
  },
  {
    path: '/vendors/:id',
    secure: true,
    exact: true,
    top: () => <TopBar />,
    main: () => <Products />,
  },
  // {
  //   path: '/vendor',
  //   secure: true,
  //   sidebar: () => null,
  //   main: () => <EditVendor />,
  // },
  // {
  //   path: '/product',
  //   secure: true,
  //   sidebar: () => null,
  //   main: () => <EditProduct />,
  // },
  // {
  //   path: '/menu',
  //   secure: true,
  //   sidebar: () => null,
  //   main: () => <Menu />,
  // },
  {
    path: '/cart',
    secure: true,
    sidebar: () => null,
    main: () => <Cart />,
  },
  {
    path: '/balance',
    secure: true,
    top: () => null,
    main: () => <AddBalance />,
  },
  {
    path: '/checkout-status',
    secure: true,
    top: () => null,
    main: () => <CheckoutStatus />,
  },
  {
    path: '/admin',
    secure: false,
    top: () => null,
    main: () => <AdminPage />,
  },
  // {
  //   path: '*',
  //   // exact: true,
  //   sidebar: () => null,
  //   main: () => <Auth />,
  // },
];

class App extends Component {
  // static propTypes = {
  //   actions: objectOf(func).isRequired,
  //   userInfo: objectOf(oneOfType([string, bool, number, object])).isRequired
  // };

  state = {
    collapsed: false,
  };

  componentDidMount() {
    const { actions } = this.props;
    const token = getTokenFromStorage();

    if (token) {
      actions.getUserFullInfo(token);
    }
  }

  render() {
    const { user, userDataLoading, modalWindowData, actions } = this.props;

    const routes = [
      ...newConfig.map((route) => (
        <CustomRoute
          secure={route.secure}
          key={route.path}
          path={route.path}
          exact={route.exact}
          component={route.main}
        />
      )),
      <Redirect to='/auth' />,
    ];

    return (
      <Fragment>
        <Header />
        <div style={{ minHeight: 'calc(100% - 222px)' }}>
          <Switch>{routes}</Switch>
        </div>
        <PaperModal {...modalWindowData} onClose={actions.closeModal} />
        <Footer />
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  user: getUserData(state),
  userDataLoading: state.user.loading,
  modalWindowData: state.modal,
});

const mapDispatchToProps = (dispatch) => ({
  actions: bindActionCreators(
    {
      getUserFullInfo,
      closeModal,
    },
    dispatch,
  ),
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
